.subscription {
  position: relative;
  padding: 120px 0 145px;
  background-color: $color-ghost-white;
  z-index: 0;

  @include vp-1199 {
    padding: 90px 0 200px;
  }

  @include vp-767 {
    padding: 53px 0 69px;
  }

  h2 {
    font-family: $ff-alumni-sans;
    font-weight: 600;
    font-size: 54px;
    line-height: 65px;
    letter-spacing: 0.03em;
    margin: 11px 6px 17px 0;

    @include vp-1199 {
      margin: 11px 16px 20px 0;
      font-size: 42px;
      line-height: 50px;
    }

    @include vp-767 {
      margin: 11px 10px 21px 0;
      font-size: 32px;
      line-height: 38px;
    }
  }

  &--JS {
    .subscription__tab-title-list {
      display: flex;
    }

    .subscription__tab-list {
      display: none;
      margin-bottom: 0;

      &--active {
        display: grid;
      }
    }
  }
}

.subscription__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}

.subscription__tab-title-list {
  position: relative;
  display: none;
  justify-content: center;
  gap: 46px;
  margin: 0 0 38px;
  padding: 0;
  list-style: none;

  @include vp-1199 {
    gap: 54px;
    margin-bottom: 43px;
  }

  @include vp-767 {
    margin-bottom: 35px;
    gap: 45px;
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0;
    background-color: transparentize($color-grey, 0.8);
  }

}

.subscription__tab-title-item {
  position: relative;

  button {
    margin: 0;
    padding: 0 0 13px;
    font-size: 20px;
    line-height: 150%;
    color: transparentize($color-sapphire, 0.5);
    border: none;
    background: none;
    cursor: pointer;

    @include vp-1199 {
      font-size: 18px;
    }

    @include vp-767 {
      padding: 0 0 14px;
      font-size: 16px;
    }
  }


  &--active {
    button {
      color: $color-sapphire;
    }

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      left: 0;
      bottom: 0;
      background-color: $color-crimson;
    }
  }
}

.subscription__tab-list {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  width: 100%;
  margin: 0 0 40px;
  padding: 0;
  list-style: none;
  z-index: 0;

  @include vp-1199 {
    max-width: 442px;
    grid-template-columns: 1fr;
  }

  @include vp-767 {
    gap: 35px;
  }
}

.subscription__tab-item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 56px 0 61px;
  background-color: $color-default-white;
  border: 7px solid $color-default-white;
  z-index: 0;

  @include vp-767 {
    padding: 43px 0 48px;
  }

  h3 {
    margin: 0 0 10px;
    font-weight: 700;
    font-size: 26px;
    line-height: 29px;
    z-index: 1;

    @include vp-767 {
      margin-bottom: 9px;
      font-size: 22px;
      line-height: 24px;
    }
  }

  p {
    margin: 0 0 83px;
    color: $color-chambray;
    z-index: 1;

    @include vp-767 {
      margin-bottom: 69px;
    }
  }

  b {
    position: relative;
    margin-bottom: 58px;
    font-weight: 700;
    font-size: 60px;
    line-height: 42px;
    color: $color-egyptian-blue;
    z-index: 0;

    @include vp-767 {
      margin-left: 7px;
      margin-bottom: 48px;
      font-size: 48px;
      line-height: 33px;
    }

    &::before {
      content: attr(data-text);
      position: absolute;
      bottom: 11px;
      left: -19px;
      font-size: 100px;
      line-height: 69px;
      color: $color-whisper;
      opacity: 0.48;
      z-index: -1;

      @include vp-767 {
        font-size: 80px;
        line-height: 55px;
      }
    }
  }

  span {
    font-family: $ff-rouble;
    color: $color-sapphire;
    font-weight: 700;
  }

  &:hover,
  &:focus-within {
    border: 7px solid transparent;
    border-image: $red-to-blue 1;

    .subscription__btn {
      color: $color-default-white;
      background: $red-to-blue-button;
      border: none;
    }
  }
}

.subscription__btn {
  width: 206px;
  height: 64px;
  color: $color-sapphire;
  border: 2px solid $color-sapphire;
  z-index: 0;

  @include vp-1199 {
    padding: 10px 20px 8px;
  }

  @include vp-767 {
    height: 66px;
    padding: 10px 20px;
  }
}

.subscription__img {
  position: absolute;
  width: 287px;
  height: 494px;
  object-fit: cover;
  object-position: left center;
  overflow: hidden;
  bottom: -125px;
  right: 0;
  z-index: 0;

  @include vp-1199 {
    width: 347px;
    height: 444px;
    bottom: -25px;
  }

  @include vp-767 {
    display: none;
  }
}
